$label-color: rgba(28, 1, 255, 1);
@mixin clearfix() {
  &::after {
    display: block;
    content: "";
    clear: both;
  }
}

@import '../backend';

.multiselect{
    &.error{
        >div{
            >div{
                border-color: red;
            }
        }
    }
    input{
        width: 100%;
    }
}
